<template>
	<div id="myData" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
    <!--登录设置-->
    <form action="" class="formLogin">
      <ul>
        <li>登录设置</li>
        <li id='userYhm'><span>用&nbsp; 户&nbsp;名：</span><input type="password" v-model="originalName" placeholder="请输入当前用户名" ></li>
        <li id='userId'><span>用&nbsp; 户&nbsp;ID：</span><input type="password" v-model="originalId" placeholder="请输入当前用户ID" ></li>
        <li id='userOldPwd'><span>原&nbsp; 密&nbsp;码：</span><input type="password" v-model="originalPsd" placeholder="请输入当前密码" ></li>
        <li id='userPwd'><span>登陆密码：</span><input type="password" v-model="newPsd" placeholder="长度必须6位以上" ></li>
        <li id='userPwd2'><span>确认密码：</span><input type="password" v-model="repeatPsd" placeholder="重复上面的密码" ></li>
      </ul>
    </form>
    <div class="commitBtn" @click="onsubmit">提交保存</div>
    <!--注意事项-->
    <div class="botTxt">
      <h4>注意事项</h4>
      <p>1、登录账号设置成功后不可以修改</p>
      <p>2、您也可以联系您的客服设置您的账户登录</p>
      <p>3、登录账号设置成功后不可以修改</p>
    </div>
    <!-- 底部导航菜单 -->
    <tabs :selected="selected"></tabs>
  </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'myData',
  data () {
    return {
      selected: '', // 导航当前页的状态
      bg: require('./../assets/images/game21.png'), // 背景图片
      originalName: '', // 当前用户名
      originalId: '', // 当前用户ID
      originalPsd: '', // 原始密码
      newPsd: '', // 新密码
      repeatPsd: '' // 重复密码
    }
  },
  components: {
    tabs, user
  },
  methods: {
    onsubmit () {
      if (this.originalName === '') {
        document.getElementById('userYhm').setAttribute('style', 'border-bottom: 1px solid red;')
      } else {
        document.getElementById('userYhm').setAttribute('style', 'border-bottom: 1px solid #7a5958;')
      }
      if (this.originalId === '') {
        document.getElementById('userId').setAttribute('style', 'border-bottom: 1px solid red;')
      } else {
        document.getElementById('userId').setAttribute('style', 'border-bottom: 1px solid #7a5958;')
      }
      if (this.originalPsd === '') {
        document.getElementById('userOldPwd').setAttribute('style', 'border-bottom: 1px solid red;')
      } else {
        document.getElementById('userOldPwd').setAttribute('style', 'border-bottom: 1px solid #7a5958;')
      }
      if (this.newPsd === '' || this.newPsd.length < 6) {
        document.getElementById('userPwd').setAttribute('style', 'border-bottom: 1px solid red;')
      } else {
        document.getElementById('userPwd').setAttribute('style', 'border-bottom: 1px solid #7a5958;')
      }
      if (this.repeatPsd === '' || (this.newPsd !== this.repeatPsd)) {
        document.getElementById('userPwd2').setAttribute('style', 'border-bottom: 1px solid red;')
      } else {
        document.getElementById('userPwd2').setAttribute('style', 'border-bottom: 1px solid #7a5958;')
      }
    }
  }
}
</script>

<style scoped>
  #myData{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;overflow-y: scroll;}
  /*表单样式*/
  .formLogin,ul{clear: both; display: inline-block; width: 100%;margin-left: -0.14rem;}
  .formLogin ul{clear: both; display: inline-block; width: 100%;margin-left: -0.14rem;}
  .formLogin ul li{display: inline-block; float: left; text-indent: 1rem;line-height: 3rem; width: 100%;border-bottom: 1px solid #7a5958;height: 3rem;font-size: 0.8rem;}
  .formLogin ul li span{ display: inline-block; color: #fff;}
  .formLogin ul li input{height: 3rem;width: 40%; line-height: 3rem;background: none;font-size: 0.8rem; color: #fff; text-indent: 0.3rem; border: 0; outline: none;}
  .formLogin ul li:nth-child(1){font-size: 1rem;color: #fff;}
  input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {color: #d8bfba;    font-size: 0.8rem;}
  input:-moz-placeholder, textarea:-moz-placeholder {color: #d8bfba;    font-size: 0.8rem;}
  input::-moz-placeholder, textarea::-moz-placeholder {color: #d8bfba;    font-size: 0.8rem;}
  input:-ms-input-placeholder, textarea:-ms-input-placeholder {color: #d8bfba;    font-size: 0.8rem;}
  .commitBtn{width: 90%; margin: 1rem auto 0 auto; border-radius:5px; text-align: center; padding: 0.6rem 0; background: #f75e32; color: #fff; font-size: 12px; outline: none;border: 0;}

  /*注意事项样式*/
  .botTxt{border-top: 1px solid #7a5958; margin-bottom: 4rem; margin-top: 1.2rem;display: inline-block; width: 94%; padding: 0 3% 2rem 3%;}
  .botTxt h4{font-size: 1rem; margin-bottom: 0.2rem; color: #fff;height: 18px;margin-top: 1rem; border-left: 3px solid #f75e32;text-indent: 12px;line-height: 18px;}
  .botTxt p{    font-size: 0.7rem; color: #fff;padding-top: 0.5rem;}

</style>



